/* eslint-disable @typescript-eslint/no-explicit-any */
import React from 'react';
import { Radio } from 'antd';
import styled from 'styled-components';
import { avatars } from '../../assets/img/index';

const AvartarPanelWrapper = styled.div`
  width: 100%;
`;

const RadioImg = styled.img`
  height: 100%;
  width: 100%;
`;

const RadioButton = styled(Radio.Button)`
  height: 0;
  padding-bottom: 14%;
  width: 14%;
  margin: 1%;
  span {
    position: absolute;
    top: 0;
    left: 0;
    padding: 0;
  }
`;

// props are injected from Antd Form
function AvatarPanel(props: any) {
  return (
    <AvartarPanelWrapper>
      <Radio.Group {...props}>
        {avatars.map((avatar, i) => {
          return (
            <RadioButton key={avatar} value={i}>
              <RadioImg src={avatar} />
            </RadioButton>
          );
        })}
      </Radio.Group>
    </AvartarPanelWrapper>
  );
}

export default AvatarPanel;
