import React, { useState, useContext } from 'react';
import { Drawer, Form, Col, Row, Input, DatePicker } from 'antd';
import AvatarPanel from '../../avatarPanel/avatarPanel';
import {
  FormWrapper,
  ConfirmBtn,
  CancelBtn,
  AddChildBtn
} from '../styled';
import { appSize } from '../../../assets/style/style';
import { ProfileStoreCtx } from '../../../store/profileStore';

const fields = {
  name: ['string', 'Child Name', 'Please enter child name'],
  iconId: ['icon', 'Icon', 'Please select an icon'],
  dob: ['date', 'Birth Day', 'Please enter child birthday'],
  fColor: ['string', 'Favourite Color', 'Please enter favourite color'],
  fVideo: ['string', 'Favourite Show', 'Please enter favourite show'],
  fCartoon: ['string', 'Favourite Cartoon', 'Please enter favourite cartoon'],
  fTemperature: ['string', 'Favourite Temperature', 'Please enter favourite temperature'],
  medicalNotes: ['string', 'Medical Notes', 'Please medical notes']
};

function ChildProfileForm() {
  const [visible, setVisible] = useState(false);
  const [form] = Form.useForm();
  const profileStore = useContext(ProfileStoreCtx);

  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };

  const onFinish = () => {
    form.validateFields()
      .then(values => {
        form.resetFields();
        
        const childProfile: ChildProfile = {
          name: values.name,
          dob: values.dob.format('MMM Do YYYY'),
          id: undefined,
          client: (profileStore.clientProfile as ClientProfile).id,
          iconId: values.iconId,
          fColor: values.fColor,
          fVideo: values.fVideo,
          fCartoon: values.fCartoon,
          fTemperature: values.fTemperature,
          averageShowerTime: 0,
          medicalNotes: values.medicalNotes
        };

        profileStore.addChildProfile(childProfile);
        onClose();
      });
  };

  const renderFormItem = (value: string[]): React.ReactNode | null => {
    switch (value[0]) {
      case 'string':
        return <Input placeholder={value[2]} />;
      case 'date':
        return <DatePicker />;
      case 'icon':
        return <AvatarPanel />;
      default:
        return null;
    }
  };

  return (
    <FormWrapper>
      <AddChildBtn onClick={showDrawer} />
      <Drawer
        title="Add New Children Profile"
        width={`calc(100% - ${appSize.appSiderWidth})`}
        onClose={onClose}
        visible={visible}
        bodyStyle={{ paddingBottom: 80 }}
        footer={
          <div>
            <CancelBtn onClick={onClose} style={{ marginRight: 8 }}>
              Cancel
            </CancelBtn>
            <ConfirmBtn onClick={onFinish} type="primary" htmlType="submit">
              Add Child
            </ConfirmBtn>
          </div>
        }
      >
        <Form
          layout="vertical"
          hideRequiredMark
          form={form}
        >
          {Object.entries(fields).map(([key, value]) => {
            return (
              <Row gutter={16} key={key}>
                <Col span={12}>
                  <Form.Item
                    name={key}
                    label={value[2]}
                    rules={[{ required: true, message: value[3] }]}
                  >
                    {renderFormItem(value)}
                  </Form.Item>
                </Col>
              </Row>
            );
          })}

        </Form>
      </Drawer>
    </FormWrapper>
  );
}

export default ChildProfileForm;
