import React, { useState } from 'react';
import { Drawer, Form, Col, Row, Input } from 'antd';
import AvatarPanel from '../../avatarPanel/avatarPanel';
import {
  FormWrapper,
  ConfirmBtn,
  CancelBtn,
  LoginBtn
} from '../styled';
import { appSize } from '../../../assets/style/style';

function SignUpForm() {
  const [visible, setVisible] = useState(false);

  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };

  return (
    <FormWrapper>
      <LoginBtn onClick={showDrawer}> Sign Up</LoginBtn>
      <Drawer
        title="Create a new account"
        width={`calc(100% - ${appSize.appSiderWidth})`}
        onClose={onClose}
        visible={visible}
        bodyStyle={{ paddingBottom: 80 }}
        footer={
          <div>
            <CancelBtn onClick={onClose} style={{ marginRight: 8 }}>
              Cancel
            </CancelBtn>
            <ConfirmBtn onClick={onClose} type="primary">
              Sign Up
            </ConfirmBtn>
          </div>
        }
      >
        <Form layout="vertical" hideRequiredMark>
          <Row gutter={24}>
            <Col span={12}>
              <Form.Item
                name="client"
                label="Client Name"
                rules={[{ required: true, message: 'Please enter client name' }]}
              >
                <Input placeholder="Please enter client name" />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={48}>
            <Col style={{ flex: 'unset', width: '100%', textAlign: 'left' }}>
              <Form.Item
                name="icon"
                label="Choose Your Icon"
                rules={[{ required: true, message: 'Please choose icon' }]}
              >
                <AvatarPanel />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={12}>
              <Form.Item
                name="password"
                label="Password"
                rules={[{ required: true, message: 'Enter your password' }]}
              >
                <Input.Password placeholder="input password" />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item
                name="recheckPassword"
                label="Enter password Again"
                rules={[{ required: true, message: 'Enter your password again' }]}
              >
                <Input.Password placeholder="input password" />
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </Drawer>
    </FormWrapper>
  );
}

export default SignUpForm;
